.schedule_container {
    overflow: hidden;
    margin: 0 auto;
    padding: 0 30px;
}

.schedule_list {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.schedule_item {
    flex: none;
    padding: 0 1.2% 3.75%;
    box-sizing: border-box;
    width: 33.33%;
}

.schedule_item:first-child,
.schedule_item:nth-child(2) {
    position: relative;
    width: 50%;
    padding: 30px 1.2% 5%;
}

.schedule_item a img {
    display: block;
    width: 100%;
    height: auto;
}

.schedule_content {
    color: #000;
}

.schedule_content h1 {
    font-size: 1.75rem;
    font-weight: 600;
    margin-top: 16px;
}

.schedule_content p {
    font-size: 1rem;
    font-weight: 400;
    line-height: 24px;
    margin-top: 8px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.schedule_content span {
    display: block;
    font-size: 1rem;
    font-weight: 700;
    line-height: 24px;
    font-style: italic;
    margin-top: 16px;
}

.schedule_content strong {
    color: #fff;
    background: #ff4949;
    border-radius: 4px;
    font-size: 11px;
    text-transform: uppercase;
    padding: 1px 4px;
    text-align: center;
    vertical-align: middle;
}

.schedule_content .schedule_days {
    background: #0068FF;
    margin: 0 2px;
}
